<template>
  <div class="bootstrap">
    <h1>{{ msg }}</h1>
    <h2>Status</h2>
    <!--<p></p>
    <h6><b-badge variant="primary"> LetÂ´s go!</b-badge> Call a Spring Boot REST backend service, by clicking a button:</h6>-->

    <p></p>
    <div class="container">
      <div class="row">
        <div class="col-lg-5">
          <b-btn variant="success" @click="callRestService(); showResponse=true" id="btnCallHello">Refresh List</b-btn>
          <p></p>
          <b-list-group v-for="(item,id) in vinnslList">
            <b-list-group-item class="d-flex justify-content-between align-items-center" @click="getDetailsById(id)" href="#">
              {{id}}
              <b-badge :variant="`${getPillByStatus(item)}`" pill>{{item}}</b-badge>
            </b-list-group-item>
          </b-list-group>
          <p></p>
        </div>

        <div class="col-lg-7">
          <div v-if="vinnslItem">
            <b-btn variant="success" @click="getDetailsById(vinnslItem.identifier); showResponse=true" id="btnItemRefresh">Refresh Item</b-btn>
            <p></p>
            <b-card-group deck>
              <b-card header="Information to Item"
                      header-tag="header"
                      footer=""
                      footer-tag=""
                      :title="`ID ${vinnslItem.identifier}`">
                <p class="card-text">{{vinnslItem.nncloud.status}}</p>
                <b-tabs>
                  <b-tab title="Definition" active>
                    <p></p>
                    <h3>Definition</h3>
                    <code>{{vinnslItem.definition | pretty}}</code>
                  </b-tab>
                  <b-tab title="Description">
                    <p></p>
                    <h3>Description</h3>
                    <code>{{vinnslItem.description}}</code>
                  </b-tab>
                  <b-tab title="Data">
                    <p></p>
                    <h3>Data</h3>
                    <code>{{vinnslItem.data}}</code>
                  </b-tab>
                  <b-tab title="Instance">
                    <p></p>
                    <h3>Instance</h3>
                    <code>{{vinnslItem.instance}}</code>
                  </b-tab>
                  <b-tab title="Result">
                    <p></p>
                    <h3>Result</h3>
                    <code>{{vinnslItem.result}}</code>
                  </b-tab>
                  <b-tab title="Status">
                    <p></p>
                    <h3>Status</h3>
                    <code>{{vinnslItem.nncloud.status}}</code>
                  </b-tab>
                  <b-tab title="Files">
                    <p></p>
                    <h3>Files</h3>
                    <code v-if="vinnslItem.data && vinnslItem.data.data && vinnslItem.data.data.file">{{vinnslItem.data.data.file}}</code>
                  </b-tab>

                </b-tabs>
                <!--  <b-button href="#"
                            variant="primary">Go somewhere</b-button>-->
              </b-card>
            </b-card-group>
          </div>
        </div>
      </div>
    </div>

    <p></p>
    <h4>Debug</h4>
    <b-btn v-b-toggle.collapse1>Show Response details</b-btn>
    <p></p>
    <b-collapse id="collapse1" class="mt-2">
    <h3>Backend response: <b-alert :show="showResponse" dismissible @dismissed="showResponse=false">{{ response }}</b-alert></h3>
      <b-card>
        <p class="card-text">The Response hat this details</p>
        <b-btn v-b-toggle.collapse1_inner size="sm" variant="primary">HTTP Status</b-btn>
        <b-collapse id=collapse1_inner class="mt-2">
          <b-card>Status: {{ httpStatusCode }}</b-card>
          <b-card>Statustext: {{ httpStatusText }}</b-card>
        </b-collapse>

        <b-btn v-b-toggle.collapse2_inner size="sm" id="btnHttpHeaders" variant="warning">HTTP Headers</b-btn>
        <b-collapse id=collapse2_inner class="mt-2">

          <p v-if="headers && headers.length">
            <li v-for="header of headers">
            <b-card>Header: {{ header.valueOf() }}</b-card>
            </li>
          </p>
        </b-collapse>

        <b-btn v-b-toggle.collapse3_inner size="sm" variant="danger">Full Request configuration</b-btn>
        <b-collapse id=collapse3_inner class="mt-2">
          <p class="card-text">Config: {{ fullResponse.config }} </p>
        </b-collapse>
      </b-card>
    </b-collapse>


    <b-tooltip target="btnHttpHeaders" title="You should always know your HTTP Headers!"></b-tooltip>

  </div>
</template>

<script>
// import axios from 'axios'
import {AXIOS} from './http-common'

export default {
  name: 'bootstrap',

  data () {
    return {
      msg: 'VINNSL-NN-UI',
      showResponse: false,
      response: '',
      vinnslList: '',
      vinnslItem: '',
      fullResponse: {
        config: {
          foo: '',
          bar: ''
        }
      },
      httpStatusCode: '',
      httpStatusText: '',
      headers: ['Noting here atm. Did you call the Service?'],
      errors: []
    }
  },
  methods: {
    // Fetches posts when the component is created.
    callRestService () {
      AXIOS.get(`//cluster.local./status`)
        .then(response => {
          // JSON responses are automatically parsed.
          this.vinnslList = response.data
          this.response = response.data
          console.log(response.data)
          this.httpStatusCode = response.status
          this.httpStatusText = response.statusText
          this.headers = response.headers
          this.fullResponse = response
        })
        .catch(e => {
          this.errors.push(e)
        })
    },
    getDetailsById (id) {
      AXIOS.get(`//cluster.local./vinnsl/` + id)
        .then(response => {
          // JSON responses are automatically parsed.
          this.vinnslItem = response.data
          this.response = response.data
          console.log('vinns/' + id)
          console.log(response.data)
          this.httpStatusCode = response.status
          this.httpStatusText = response.statusText
          this.headers = response.headers
          this.fullResponse = response
        })
        .catch(e => {
          this.errors.push(e)
        })
    },
    getPillByStatus (status) {
      console.log(status)
      if (status === 'INPROGRESS') {
        return 'warning'
      }
      if (status === 'FINISHED') {
        return 'success'
      }
      if (status === 'ERROR') {
        return 'danger'
      }
      return 'primary'
    }
  }
}
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
p {
  margin-bottom: 20px;
}

h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  /*color: #42b983;*/
}
</style>

<!--
/*,
  filters: {
    pretty: function (value) {
      return JSON.stringify(JSON.parse(value), null, 2)
    }
  }*/-->



